import React from 'react'
import Particles from 'react-tsparticles'
import type { Engine } from 'tsparticles-engine'
import { loadSlim } from 'tsparticles-slim'

const particleStyle = {
  position: 'absolute',
  top: 0,
  left: 0,
  overflow: 'hidden',
} as React.CSSProperties

export class ParticlesContainer extends React.Component {

  async customInit(engine: Engine): Promise<void> {
    // this adds the bundle to tsParticles
    await loadSlim(engine);
  }

  render() {
    return(
      <>
        <Particles
          style={particleStyle}
          init={this.customInit}
          id="tsparticles"
          options={{
            fullScreen: false,
            fpsLimit: 120,
            interactivity: {
              detectsOn: "window",
              events: {
                onClick: {
                  enable: false,
                  mode: 'push'
                },
                onhover: {
                  enable: true,
                  mode: 'slow'
                }
              },
              modes: {
                push: {
                  quantity: 3
                },
                grab: {
                  distance: 50,
                  line_linked: {
                    opacity: 0.1
                  }
                },
                "repulse": {
                  "distance": 200,
                  "duration": 0.8,
                  "factor": 100,
                  "speed": 1,
                  "maxSpeed": 50,
                  "easing": "ease-out-quad"
                },
                slow: {
                  factor: 1.5,
                  radius: 150
                },
              }
            },
            // background: {
            //   color: {
            //     value: "#00274C",
            //   }
            // },
            particles: {
              color: {
                value: '#ffffff'
              },
              links: {
                color: '#ffffff',
                distance: 50,
                enable: true,
                opacity: 0.5,
                width: 1
              },
              collisions: {
                enable: false
              },
              move: {
                drift: .001,
                direction: 'right',
                enable: true,
                outModes: {
                  default: 'out'
                },
                decay: 0,
                random: false,
                speed: 5,
                straight: false
              },
              number: {
                density: {
                  enable: true,
                  area: 800
                },
                value: 200
              },
              opacity: {
                value: 0.5
              },
              shape: {
                type: ['circle','triangle','square']
              },
              size: {
                random: true,
                value: 5
              }
            },
            detectRetina: true
          }}
        />
      </>
    )
  }
}